import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

const initialSins = [
  { name: 'Pride', verse: 'Proverbs 16:18 - Pride goes before destruction, a haughty spirit before a fall.', isOvercome: false },
  { name: 'Envy', verse: 'James 3:16 - Where you have envy and selfish ambition, there you find disorder and every evil practice.', isOvercome: false },
  { name: 'Wrath', verse: 'Ecclesiastes 7:9 - Do not be quickly provoked in your spirit, for anger resides in the lap of fools.', isOvercome: false },
  { name: 'Lust', verse: '1 John 2:16 - The lust of the flesh and eyes comes not from the Father but from the world.', isOvercome: false },
  { name: 'Gluttony', verse: 'Proverbs 23:20‚Äì21 - Do not join those who drink too much wine or gorge themselves on meat.', isOvercome: false },
  { name: 'Greed', verse: 'Luke 12:15 - Be on your guard against all kinds of greed; life does not consist in an abundance of possessions.', isOvercome: false },
  { name: 'Sloth', verse: 'Proverbs 19:15 - Laziness brings on deep sleep, and the shiftless go hungry.', isOvercome: false },
];

const SevenDeadlySins = () => {
  const navigate = useNavigate();
  const [sins, setSins] = useState(initialSins);
  const [showVerse, setShowVerse] = useState(Array(initialSins.length).fill(false));

  const toggleOvercome = (index) => {
    const updated = [...sins];
    updated[index].isOvercome = !updated[index].isOvercome;
    setSins(updated);
  };

  const toggleVerse = (index) => {
    const updated = [...showVerse];
    updated[index] = !updated[index];
    setShowVerse(updated);
  };

  return (
    <div style={{ padding: '1rem', maxWidth: '600px', margin: '0 auto' }}>
      <h2>The Seven Deadly Sins</h2>
      <p style={{ fontStyle: 'italic', marginBottom: '1rem' }}>
        Tap each sin to reveal its corresponding Bible verse and track your struggle or victory.
      </p>
      <ul style={{ padding: 0, listStyle: 'none' }}>
        {sins.map((sin, index) => (
          <li key={index} style={{ marginBottom: '20px' }}>
            <div>
              <strong>{sin.name}</strong>
              <div style={{ marginTop: '0.5rem' }}>
                <button onClick={() => toggleOvercome(index)}>
                  {sin.isOvercome ? 'Overcome' : 'Struggling'}
                </button>
                <button onClick={() => toggleVerse(index)} style={{ marginLeft: '10px' }}>
                  {showVerse[index] ? 'Hide Verse' : 'Show Verse'}
                </button>
              </div>
              {showVerse[index] && (
                <p style={{ marginTop: '0.5rem', color: '#f39c12' }}>
                  {sin.verse}
                </p>
              )}
              {sin.isOvercome && (
                <p style={{ color: 'green', fontStyle: 'italic', marginTop: '0.5rem' }}>
                  You've overcome this sin! Keep growing in the Lord.
                </p>
              )}
            </div>
          </li>
        ))}
      </ul>

      <button onClick={() => navigate('/')}>Back to Home</button>
    </div>
  );
};

export default SevenDeadlySins;
